/* área total da tela */
.container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #f7f7f7;
}

/* centraliza tudo */
.main {
  display: flex;
  flex-direction: column;
  gap: 25px;
  align-items: center;
}

/* logo */
.logo {
  margin-bottom: 10px;
}

/* formulário */
.form {
  display: flex;
  flex-direction: column;
  width: 280px;
  gap: 10px;
}

/* ✅ seletor válido (não é global) */
.form input {
  padding: 12px;
  border-radius: 6px;
  border: 1px solid #b3b3b3;
  outline: none;
}

/* botão */
.btnPrimary {
  margin-top: 10px;
  padding: 12px;
  background: #000;
  color: #fff;
  border-radius: 6px;
  cursor: pointer;
  border: none;
}

/* link */
.link {
  margin-top: 10px;
  font-size: 14px;
  cursor: pointer;
  color: #0070f3;
  text-decoration: none;
}

.link:hover {
  text-decoration: underline;
}
